@import "_reset";

$mainColor : #ff0000;

.red{color: $mainColor}

@mixin fnAlert() {
  background-color: #eee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include fnAlert();
}
.my-alert2 {
  @extend .my-alert;
  background: #ffe591;
}

.my-alert p {
  margin-bottom: 0;
}